.card {
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 25px 3px rgba(0, 0, 0, 0.75);
  cursor: default; }
  .card .container {
    width: 100%;
    height: 250px; }
    .card .container:hover .back {
      height: 100%; }
      .card .container:hover .back .content {
        opacity: 1; }
        .card .container:hover .back .content .id {
          opacity: 1; }
    .card .container .front {
      height: 100%;
      width: 100%;
      display: flex;
      background-image: url("https://images.pexels.com/photos/998641/pexels-photo-998641.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940");
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat; }
      .card .container .front .content {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        padding: 0 2rem; }
        .card .container .front .content .character_image {
          height: 100px;
          width: 100px;
          border-radius: 50%;
          margin: 0 0 1rem; }
        .card .container .front .content .character_name {
          margin: 0.5rem 0 0.25rem;
          color: white;
          text-align: center; }
        .card .container .front .content .character_type {
          font-size: 1.2rem;
          color: white; }
          .card .container .front .content .character_type .species {
            padding: 0 0.25rem;
            font-family: "Gruppo", cursive;
            text-transform: capitalize; }
          .card .container .front .content .character_type .gender {
            padding: 0 0.25rem;
            font-family: "Gruppo", cursive;
            text-transform: capitalize; }
    .card .container .back {
      background-color: #d6d6d6;
      opacity: 0.9;
      transition: all 0.4s ease-in-out;
      height: 0;
      max-width: 100%;
      z-index: 500;
      position: relative;
      top: -100%; }
      .card .container .back .content {
        height: 0;
        opacity: 0;
        transition: all 0.4s ease-in-out;
        padding: 2rem; }
        .card .container .back .content p {
          margin-bottom: 0.5rem;
          color: #272727;
          font-family: "Gruppo", cursive;
          line-height: 1.2;
          text-transform: capitalize;
          font-size: 1.025rem; }
          .card .container .back .content p span {
            margin-right: 0.25rem;
            font-size: 0.925rem; }
        .card .container .back .content .id {
          opacity: 0;
          transition: all 0.4s ease-in-out;
          position: absolute;
          bottom: 2rem;
          right: 2rem;
          font-size: 0.75rem;
          background: #35b7eb;
          padding: 0.25rem 0.5rem;
          border-radius: 1rem;
          color: white; }

